.gallery {
  position: relative;

  &__wrapper {
    padding: 0 10px;
    
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(6, 100px);
    grid-gap: 10px;

    @include big-tablet {
      grid-template-rows: repeat(6, 85px);
    }

    @include small-tablet {
      grid-template-rows: repeat(6, 50px);
    }

    @include small-mobile {
      grid-template-rows: repeat(9, 50px);
    }
  }

  &__images {
    position: relative;
    height: 100%;
    width: 100%;
    transition: all .3s ease-in-out;
  }

  &__item {
    position: relative;
    overflow: hidden;
    grid-column: span 3;
    grid-row: span 2;
    background-color: var(--color-primary);
    transition: all .3s ease-in-out;
    cursor: pointer;

    @include small-mobile {
      grid-column: span 6 !important;
      grid-row: span 3 !important;
    }
      
    &-play {
      position: absolute;
        transform: translate(-50%, -50%);
        left: 50%;
        top: 50%;
        width: 92px;
        height: 92px;
        border-radius: 100%;
        // background-color: #eba79e;
        background-color: var(--color-light);
        z-index: 2;
        opacity: 1;
        transition: background-color .3s;
        cursor: pointer;
      
        &:hover {
          transition: background-color .3s;
          background-color: #c9c8c8;
          
          svg {
            fill: var(--color-light);
            transition: fill .3s;
          }
        }
      
        span {
          position: absolute;
          transform: translate(-40%, -40%);
          left: 50%;
          top: 50%;
      
          svg {
            width: 30px;
            height: 30px;
            fill: var(--color-primary);
            transition: fill .3s;
          }
        }

        @include mobile {
          width: 70px;
          height: 70px;
        }
    }

    &--3 {
      grid-column: span 6;
      grid-row: span 4;

      @include small-mobile {
        order: 1;
      }
    }

    &--4 {
      grid-column: span 6;
      grid-row: span 4;
    }

    &--5 {
      @include small-mobile {
        order: 2;
      }
    }

    &--6 {
      @include small-mobile {
        order: 2;
      }
    }
      
    
    &::after {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: black;
        background-image: url("../img/zoom.svg");
        background-repeat: no-repeat;
        background-size: 70px 70px;
        background-position: center center;
        opacity: 0;
        z-index: -1;
        transition: all .3s;
      }      

      &:hover {
        
        
        &::after { 
          opacity: 0.5;
          z-index: 1;
          transition: all .3s;
        }         
        
        .gallery__images {
          transform: scale(1.05);
          transition: all .3s ease-in-out;
          //z-index: 2;
        }         
      }

    &-inner {
      font-weight: 500;
      line-height: 30px;
      font-size: 18px;
      text-align: center;
      letter-spacing: 2.5px;
      color: #1d1d1b;

      span {
        text-transform: uppercase;
      }
    }

    &-salon {
      font-size: 26px;
      font-style: italic;
      color: var(--color-primary);
    }
  }  
}

.gallery--container {
  position: relative;
  z-index: 5;
}

.gallery__item--1::after {
  opacity: 0.9;
  z-index: 1;
  background-color: var(--color-primary);
}

.gallery__item--3::after {
  opacity: 0.9;
  z-index: 1;
  background-color: var(--color-primary);
}