.about {
  padding-bottom: 0;

  &__inner {
    @include flex-all-sb;
    margin-bottom: 70px;

    @include mini-desk {
      display: block;
    }
  }

  &__left {
    max-width: 500px;
    margin-right: 80px;

    @include mini-desk {
      max-width: 100%;      
    }

    @include tablet {
      margin-right: 0;;      
    }
  }

  &__right {
    position: relative;
    max-width: 1210px;
    max-height: 800px;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      display: block;
      opacity: 0;
      background-color: #000;
      transition: opacity .3s;
    }

    &:hover {
      &:after {
        transition: opacity .3s;
        opacity: .5;
      }
    }

    img {
      @include mini-desk {
        width: 100%;
      }
    }

    &-play {
      position: absolute;
      transform: translate(-50%, -50%);
      left: 50%;
      top: 50%;
      width: 92px;
      height: 92px;
      border-radius: 100%;
      background-color: var(--color-primary); 
      z-index: 2;  
      opacity: 1; 
      transition: background-color .3s;
      cursor: pointer; 

      &:hover {
        transition: background-color .3s;
        background-color: #c9c8c8;
          
      }
      
      span {
        position: absolute;
        transform: translate(-40%, -40%);
        left: 50%;
        top: 50%;
        svg {
          width: 30px;
          height: 30px;
          fill: var(--color-light);
        }
      }
    }

    /* &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: .5;
        color: black;
      } */

      @include mini-desk {
        max-width: 100%;
      }
  }

  &__title {
    position: relative;
  }

  &__advent {
    display: none;
    padding: 70px 0;
    background-color: #fafafa;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-descr {
      letter-spacing: 0.5px;
      padding-top: 20px;
      margin: 0;
    }
  }

  &__item {
    @include flex-column-center;

    text-align: center;
    width: 110px;

    &-svg {     
      @include flex-all-center;

      position: relative;
      width: 70px;
      height: 70px;
      border-radius: 100%;
      background-color: #ffe5db;
    }

    svg {
      position: absolute;
      top: 7px;
      right: 30px;
      width: 60px;
      height: 60px;
      fill: #2c2c2c;
    }
  }
}