.contacts {
  padding-bottom: 0;
  background-color: var(--color-black);
  border-bottom: 1px solid #e2aa941f;

  &__title {
    color: var(--color-light);
  }

  &__info {
    display: flex;  
    
    @include big-tablet {
      display: block;
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    @include mini-desk {
      width: 50%;
    }

    @include big-tablet {
      width: 100% ;
    }
  }

  &__items {
      display: flex;
      justify-content: space-between;
      font-style: normal;

      @include mini-desk {
        flex-wrap: wrap;
        justify-content: center;
      }

      @include big-tablet {
        display: flex;      
      }
    }
  
    &__item {
      padding: 0 20px;
      margin-bottom: 30px;
      width: 260px;
      height: auto;
      text-align: center;
  
      &-icon {
        position: relative;
        margin: 0 auto;
        margin-bottom: 20px;
        height: 50px;
        width: 50px;
        border-radius: 50%;
        background-color: #373737;
      }
  
      &-svg {
        position: absolute;
        top: 5px;
        left: -15px;
        width: 40px;
        height: 40px;
        fill: var(--color-primary);
      }
  
      &-title {
        margin: 20px 0;
        font-size: 28px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 1.5px;
        line-height: 1.3;
        color: var(--color-light);
        
        @include tablet {
          font-size: 20px;
          letter-spacing: 1.5px;
        }
      }
  
      &-descr {
        margin: 0;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 2.5px;
      }
    }

  &__maps {
    display: flex;
    justify-content: space-between;

    @include big-tablet {
      flex-wrap: wrap;
    }
  
      &-wrapper {
        position: relative;
        min-height: 600px;
        width: 50%;

        @include big-tablet {
          width: 100% !important;
          margin-bottom: 10px !important;
          min-height: 300px !important;
        }
      }
  
      &-text {
        position: absolute;
        width: 400px;
        padding: 50px;
        top: 50%;
        left: 1%;
        background-color: #ffffff;
        box-shadow: 0px 10px 60px 0px rgb(0 0 0 / 7%);
        z-index: 1;
  
        h4 {  
          margin: 0;
          margin-bottom: 14px;
          width: 100%;
          color: var(--color-primary);
          font-weight: 700;
          font-size: 16px;
          line-height: 16px;
          letter-spacing: 2.5px;
          text-align: left;
        }
  
        p {
          margin: 0;
          color: var(--color-black);
          font-weight: 700;
          line-height: 1.3;
          //text-transform: uppercase;
          letter-spacing: 1.5px;
          font-size: 20px;
          //line-height: 20px;
        }
  
        &:last-child {
          .info__maps-text {
            right: -20%;
          }
        }

        @include big-tablet {
          display: none;
        }
      }
  }
}

.social {
  @include flex-all-center;

  padding: 60px 0;

  &__item {
    margin-bottom: 15px;

    &:hover {
      svg {
        fill: var(--color-primary);
        scale: 0.9;
        transform: translateY(-2px);
        transition: all .3s;
      }
    }

    &:not(:last-child) {
      margin-right: 30px;
    }

    &-svg {
      @include flex-all-center;

      height: 48px;
      width: 48px;
      border-radius: 100%;
      background-color: #373737;

      svg {
        position: relative;
        height: 28px;
        width: 28px;
        fill: #f5f5f5;
        transition: all .3s;
      }
    }

    @include small-mobile {
      margin-right: 10px !important;
    }
  }
}

.contacts-form {
  margin-bottom: 70px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: transparent;
  border-radius: 4px;
  padding: 48px 40px;
  height: fit-content;
  width: 495px;
  z-index: 2;
  //box-shadow: 0px 10px 60px 0px rgb(0 0 0 / 7%);
  /* box-shadow:
    0px 60px 80px -20px rgba(30, 33, 44, 0.16),
    0px 26px 24px -10px rgba(30, 33, 44, 0.1),
    0px 12px 10px -6px rgba(30, 33, 44, 0.08),
    0px 4px 4px -4px rgba(30, 33, 44, 0.05); */



  &__title {
    margin: 0;
    margin-bottom: 14px;
    width: 100%;
    font-weight: 700;
    //font-size: 28px;
    //line-height: 150%;
    text-align: left;
    text-transform: uppercase;
    color: var(--color-primary);
  }

  &__subtitle {
    color: var(--color-light);
  }

  &__field {
    margin-bottom: 21px;
  }

  &__checkbox {
    margin-bottom: 22px;
  }

  &__btn {
    padding: 12px 31px;
    width: 100%;
    font-size: 14px;
    letter-spacing: 0.5px;
    transition: all .3s;

    &:hover {
      background-color: transparent;
      border-color: var(--color-primary);
      color: var(--color-primary);
      transition: all .3s;
      cursor: pointer;
    }
  }

  @include big-tablet {
    padding: 48px 0px;
    width: 100%;
  }
}

